* {
    border: none;
}

.container {
    padding: 15px;
    gap: 16px;
    display: flex;
    border-color: #ffffff;
    align-items: center;
    justify-content: center;
}

.story-elements {
    cursor: pointer;
}

.user-elements div {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(#c82d8e, #f99d4c);

    font-size: 16px;
}

.image-user-story {
    border: 2px solid white;
    width: 56px;
    height: 56px;
    border-radius: 50%;
}

.container .user-elements span {
    display: flex;
    font-size: 13px;
    align-items: center;
    justify-content: center;
}