.mainGrid {
    display: grid;
    grid-template-areas: "firstColumn secondColumn";
    grid-template-columns: 480px 235px;
    grid-gap: 25px;
    max-width: 957px;
    margin: 100px auto;
    padding-left: 50px;
    margin-top: 100px;
}

.first-column {
    border: none;
}

.box {
    background-color: rgb(255, 255, 255);
    border: 1px solid #dbdbdb;
}

.box2 {
    background-color: rgb(255, 255, 255);
    border: 1px solid #dbdbdb;
}

.suggestionBox {
    flex: 1;
    max-width: 293px;
    position: fixed;
}

.box_sugestao {
    background: var(--main);
    height: auto;
    position: flex;
}

.sugestoes {
    display: flex;
    align-items: center;
    gap: 20px;
}

.sugestao_de_usuario {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.sugestoes img {
    height: 65px;
    width: 65px;
    border-radius: 50%;
    cursor: pointer;
}

.vertudo {
    display: flex;
    justify-content: space-between;
    flex: 1;
}

.vertudo span {
    cursor: pointer;
}

.vertudo p {
    color: var(--secondary);
}

.sugestoes span {
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
}

.button {
    font-weight: 500;
    color: var(--quartary);
    cursor: pointer;
}

.sugestion {
    margin-top: 5px;
}

.informacao img {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    cursor: pointer;
}

.informacao {
    margin: 15px 0 0 5px;
    display: flex;
    align-items: center;
    gap: 16px;
}

.info_sugestao {
    flex: 1;
}

.informacao span {
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
}

.informacao p {
    font-size: 13px;
    color: var(--secondary);
    margin-top: 3px;
}

.seguir {
    font-weight: 500;
    color: var(--quartary);
    cursor: pointer;
}

.final {
    margin-top: 25px;
    flex: 1;
}

.final p {
    font-size: 12px;
    color: var(--terciary);
    margin-bottom: 15px;
    cursor: pointer;
}