.header-post {
    display: flex;
    align-items: center;
    padding: 10px 15px;
    justify-content: space-between;
}

.header-post .img-header-post {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.infos-post {
    display: flex;
    align-items: center;
    gap: 16px;  
}

.img-post img {
    width: 100%;
    object-fit: cover;
}

.footer-post {
    padding: 10px 15px;
}

.engagement-post {
    display: flex;
    justify-content: space-between;
}

.engagement-post .icons-1 {
    display: flex;
    gap: 16px;
}

.icon {
    cursor: pointer;
}

.like {
    margin-top: 6px;
}

.like span {
    font-size: 15px;
    font-weight: 500;
}

.legend {
    margin: 6px 0 3px;
}

.legend .p span {
    font-weight: 500;
}

.time-post time {
     font-size: 11px;
     color: rgb(41, 41, 41);
}

.coment{
    border-top: 1px solid rgb(41, 41, 41);
    margin-top: 8px;

    display: flex;
}

.fake-coment {
    padding: 15px 0;
    display: flex;
    align-items: center;
    
    flex: 1;
    gap: 16px;
}

.fake-coment input{
    font-size: 16px;
}

.coment button {
    color: rgb(62, 62, 240);
    font-weight: 500;
    cursor: pointer;
}