* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    outline: 0;
    background: none;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.box {
  border-radius: 5px;
  margin-bottom: 16px;
}

.app__header {

    background-color: #FFFFFF;
    border-bottom: 1px solid lightgray;

    position: fixed;
    top: 0;
    width: 100%;
  }

.app__header .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    height: 60px;
    max-width: 957px;
    margin: 0 auto;
    width: 90%;
  }

.app__header .container .logo {
    width: 100px;
    cursor: pointer;
}

.input {
    display: flex;
    background-color: #EFEFEF;
    width: 263px;
    height: 34px;
    border-radius: 5px;
    font-family: 'Roboto', sans-serif, Thin;
    justify-content: center;
    flex-direction: column;
}

.search-icon {
  display: flex;
  gap: 10px;
  padding-left: 10px;
}

.menu-icons {
   display: flex;
   gap: 16px;
   cursor: pointer;
   align-items: center;
}

.menu-icons .img-user {
  width: 26px;
  height: 26px;
  border-radius: 50%;
}